/***********************************
  Mobile Single Game Page Styling
************************************/

/* colors used:
  #fdfdff
  #ddd
  #02558b
  #2cf
  #ff533d
*/

.singleGame {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 90vh;
}

.singleGame .card {
  border: none;
  margin: 0rem auto;
  padding: 0rem;
  width: 100%;
}

.singleGame .title_block {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 48vh;
  padding-top: 2rem;
}

.arrow_container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-right: 1rem;
  -webkit-animation: 1s ease-out 0s 1 slideInFromRight;
          animation: 1s ease-out 0s 1 slideInFromRight;
}

@-webkit-keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

}

@keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

}

.material-icons.md-36 {
  color: gray;
  font-size: 36px;
}

.title_block {
  background-image: url('../../images/gplaypattern.png');
}

.title_block h2 {
  color: #02558b;
}

.title_block a {
  color: gray;
}

.singleGame h3 {
  margin-bottom: 0rem;
}

.singleGame .game_category {
  padding-top: 0rem;
}

.icon_bar {
  /* border-bottom: 1px solid lightgray; */
  border-radius: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 2rem auto;
  padding: 1rem;
  width: 100%;
}

.icon_bar div {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
}

.icon_bar i {
  color: #ff533d;
  font-size: 42px;
  margin-right: 0rem;
  text-align: center;
}

.icon_bar p {
  color: gray;
  font-size: 0.9rem;
  margin-bottom: 0rem;
  text-align: center;
  text-transform: capitalize;
}

.icon_bar #card {
  transform: rotate(180deg);
  /* Safari */
  -webkit-transform: rotate(180deg);
  /* Firefox */
  -moz-transform: rotate(180deg);
  /* IE */
  -ms-transform: rotate(180deg);
  /* Opera */
  -o-transform: rotate(180deg);
}

.game_objective {
  /* background: linear-gradient(155deg, #02558b, #2cf); */
  border-radius: 0px;
  color: white;
  color: gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  min-height: 7rem;
  text-align: left;
}

.game_objective h5 {
  color: #02558b;
}

.rules-buttons {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 3rem auto 0rem;
  width: 100%;
}

.rules-buttons .btn {
  /* background-color: #ff533d; */
  background-color: #eee;
  border: none;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  /* color: white; */
  color: #ff533d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  font-size: 1.3rem;
  height: 3rem;
  -webkit-transition: background-color 0.4s, color 0.4s;
  -o-transition: background-color 0.4s, color 0.4s;
  transition: background-color 0.4s, color 0.4s;
}

.rules-buttons .btn.active {
  background-color: #02558b;
  border: none;
  color: white;
  -webkit-transition: background-color 0.4s, color 0.4s;
  -o-transition: background-color 0.4s, color 0.4s;
  transition: background-color 0.4s, color 0.4s;
}

.btn i {
  margin-right: 0px;
}

.material-icons.rotate {
  transform: rotate(135deg);
  /* Safari */
  -webkit-transform: rotate(135deg);
  /* Firefox */
  -moz-transform: rotate(135deg);
  /* IE */
  -ms-transform: rotate(135deg);
  /* Opera */
  -o-transform: rotate(135deg);

  -webkit-transition: -webkit-transform 0.5s;

  transition: -webkit-transform 0.5s;

  -o-transition: transform 0.5s;

  transition: transform 0.5s;

  transition: transform 0.5s, -webkit-transform 0.5s;
  }

.alert h4 {
  font-size: 1.7rem;
  text-transform: capitalize;
}

.alert {
  margin-bottom: 0rem;
}

#delete_button {
  -ms-flex-item-align: center;
      align-self: center;
  /*background-color: #ff533d;*/
  background-color: transparent;
  color: white;
  /*margin-top: 2rem;*/
  width: 20%;
}

.footer {
  background: -webkit-linear-gradient(295deg, #02558b, #2cf);
  background: -o-linear-gradient(295deg, #02558b, #2cf);
  background: linear-gradient(155deg, #02558b, #2cf);
  height: 3rem;
}



